<template>
  <div class="home" id="home" name="home">
    <!-- 轮播图 -->
    <el-carousel :interval="2500" type="card" height="320px">
      <el-carousel-item v-for="item in carousel" :key="item.carousel_id">
        <img style="height:320px;width: 100%" :src="item.product_picture"     />
      </el-carousel-item>
    </el-carousel>
    <!-- 轮播图END -->
    <div class="main-box">
      <div class="main">
        <!-- 手机商品展示区域 -->
        <div class="phone"><!--该class没卵用-->
          <div class="box-hd">
            <div class="title"><b>异火</b></div>
          </div>
          <div class="box-bd">
<!--            <div class="promo-list">-->
<!--              <router-link to>-->
<!--                <img src="../assets/imgs/FJ.png" />-->
<!--              </router-link>-->
<!--            </div>-->
            <div class="list">
              <MyList :list="phoneList" :isMore="true"></MyList>
            </div>
          </div>
        </div>
        <!-- 手机商品展示区域END -->

        <!-- 家电商品展示区域 -->
        <div class="appliance" id="promo-menu">
          <div class="box-hd">
            <div class="title"><b>功法</b></div>
<!--            <div class="more" id="more">-->
<!--              <MyMenu :val="2" @fromChild="getChildMsg">-->
<!--                <span slot="1">热门</span>-->
<!--                <span slot="2">电视影音</span>-->
<!--              </MyMenu>-->
<!--            </div>-->
            <div class="more" id="more">
              <MyMenu :val="4" @fromChild="getChildMsg2">
                <span slot="1">天阶</span>
                <span slot="2">地阶</span>
                <span slot="3">玄阶</span>
                <span slot="4">黄阶</span>
              </MyMenu>
            </div>
          </div>
          <div class="box-bd">
<!--            <div class="promo-list">-->
<!--              <ul>-->
<!--                <li>-->
<!--                  <img src="../assets/imgs/FJ.png" />-->
<!--                </li>-->
<!--                <li>-->
<!--                  <img src="../assets/imgs/FJ.png" />-->
<!--                </li>-->
<!--              </ul>-->
<!--            </div>-->
            <div class="list">
              <MyList :list="applianceList" :isMore="true"></MyList>
            </div>
          </div>
        </div>
        <!-- 家电商品展示区域END -->

        <!-- 配件商品展示区域 -->
        <div class="accessory" id="promo-menu">
          <div class="box-hd">
            <div class="title"><strong>药鼎</strong></div>
 
          </div>
          <div class="box-bd">
 
            <div class="list">
              <MyList :list="accessoryList" :isMore="true"></MyList>
            </div>
          </div>
        </div>
        <!-- 配件商品展示区域END -->

        <div class="accessory" id="promo-menu">
          <div class="box-hd">
            <div class="title"><strong>丹药</strong></div>
     
          </div>
          <div class="box-bd">
 
            <div class="list">
              <MyList :list="danyao" :isMore="true"></MyList>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      carousel:[{carousel_id:1,product_picture:require("../assets/imgs/A.jpg")},{carousel_id:2,product_picture:require("../assets/imgs/C.png")},{carousel_id:3,product_picture:require("../assets/imgs/B.jpg")}],// [{carousel_id:1,imgPath:require("2017/09/25/13/12/cocker-spaniel-2785074_960_720.jpg")}], // 轮播图数据
       alllist:"",
                                            danyao:"",
                                            phoneList: "", // 手机商品列表
      miTvList: "", // 小米电视商品列表
                                            applianceList: "", // 家电商品列表
      applianceHotList: "", //热门家电商品列表
                                            accessoryList: "", //配件商品列表
      accessoryHotList: "", //热门配件商品列表
      protectingShellList: "", // 保护套商品列表
      chargerList: "", //充电器商品列表
      applianceActive: 1, // 家电当前选中的商品分类
      accessoryActive: 1 // 配件当前选中的商品分类
      

    };
  },
    computed: {
      listcom() {
        return this.alllist.filter(function(val) {
          //只返回id是偶数的
          return this.alllist.category_id === val;
        })
      }
    },
  watch: {
    // 家电当前选中的商品分类，响应不同的商品数据
    applianceActive: function(val) {
      // 页面初始化的时候把applianceHotList(热门家电商品列表)直接赋值给applianceList(家电商品列表)
      // 所以在切换商品列表时判断applianceHotList是否为空,为空则是第一次切换,把applianceList赋值给applianceHotList
      if (this.applianceHotList == "") {
        this.applianceHotList = this.applianceList;
      }
      if (val == 1) {
        // 1为热门商品
        this.applianceList = this.applianceHotList;
        return;
      }
      if (val == 2) {
        // 2为电视商品
        this.applianceList = this.miTvList;
        return;
      }
    },
    accessoryActive: function(val) {
      // 页面初始化的时候把accessoryHotList(热门配件商品列表)直接赋值给accessoryList(配件商品列表)
      // 所以在切换商品列表时判断accessoryHotList是否为空,为空则是第一次切换,把accessoryList赋值给accessoryHotList
      if (this.accessoryHotList == "") {
        this.accessoryHotList = this.accessoryList;
      }
      if (val == 1) {
        // 1为热门商品
        this.accessoryList = this.accessoryHotList;
        return;
      }
      if (val == 2) {
        // 2为保护套商品
        this.accessoryList = this.protectingShellList;
        return;
      }
      if (val == 3) {
        //3 为充电器商品
        this.accessoryList = this.chargerList;
        return;
      }
    }
  },
  created() {

     this.getPromo( "phoneList",15);
    this.getPromo(  "applianceList",16);
    this.getPromo( 'accessoryList',17);
    this.getPromo( "danyao",17);
  },
  methods: {
    evenNumbers: function (cateid) {
            return this.alllist.filter(function (alllist) {
            return alllist.category_id === cateid;
            })
        },
    // 获取家电模块子组件传过来的数据
    getChildMsg(val) {
      this.applianceActive = val;
    },
    // 获取配件模块子组件传过来的数据
    getChildMsg2(val) {
      this.accessoryActive = val;
    },
    getall(){

    },
    // 获取各类商品数据方法封装
    getPromo(name ,val) {
       this.$axios(   
            {
              url: this.$target+"/goodsByCategoryId", 
              method: 'get',
              params:{
              goodsCategoryId: val,
              currentPage: 1,
              pageSize: 5,
              }
        })
        .then(res => {
           this[name] = res.data.data;
           this[name].splice(2)
        })
        .catch(err => {
          return Promise.reject(err);
        });     
    }
  }
};
</script>
<style scoped>
@import "../assets/css/index.css";
</style>